<template>
  <div class="home">
    <!-- <HelloWorld msg="Welcome to Your Vue.js App"/> -->
    <v-head></v-head>
    <!--  -->
    <div :class="['content-box']">
      <!-- <v-tags></v-tags> -->
      <transition name="move" mode="out-in">
        <keep-alive :include="tagsList">
          <router-view></router-view>
        </keep-alive>
      </transition>
    </div>
    <!--  -->
    <v-sidebar></v-sidebar>
    <!--  -->
    <v-editor></v-editor>
  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from '@/views/HelloWorld.vue'
import vHead from './Header.vue';
import vSidebar from './Sidebar.vue';
import vEditor from './Editor.vue';
import vTags from './Tags.vue';
import bus from './bus';

export default {
  name: 'home',
  data() {
    return {
      tagsList: [],
      collapse: true,
      isSidebarShown: true
    }
  },
  components: {
    // HelloWorld,
    vHead,vSidebar,vEditor,vTags
  },
  created() {
    //  keep-alive
    bus.$on('tags', msg => {
      let arr = []
      for(let item in msg) {
        msg[item].path && arr.push(msg[item].name);
      }
      //console.log(arr)
      this.tagsList = arr
    });

    // window
    window.onresize = function (e) {
      bus.$emit('editorResize', e.currentTarget);
      bus.$emit('mapResize', e.currentTarget);
      bus.$emit('sidebarResize', e.currentTarget);
      bus.$emit('dashboardResize', e.currentTarget);
      bus.$emit('communityResize',e.currentTarget);
    }
  }
}
</script>

<style>
.content-box {
  position: absolute;
  left: 0;
  right: 0;
  top: 50px;
  bottom: 0;
  /* overflow-y: scroll; */
  -webkit-transition: left .3s ease-in-out;
  transition: left .3s ease-in-out;
  background: #f0f0f0;
}
</style>

