<template>
  <div class="sidebar" v-show="isSidebarShown">
    <el-menu class="sidebar-el-menu" :default-active="onRoutes" :collapse="collapse" background-color="#324157"
      text-color="#bfcbd9" active-text-color="#20a0ff" unique-opened router>
      <template v-for="item in items">
        <!--  -->
        <template v-if="item.subs">
          <el-submenu :index="item.index" :key="item.index" v-show="item.vshow">
            <template slot="title">
              <i :class="item.icon"></i><span slot="title">{{ item.title }}</span>
            </template>
            <el-menu-item v-for="(subItem,i) in item.subs" :key="i" :index="subItem.index">
              {{ subItem.title }}
            </el-menu-item>
          </el-submenu>
        </template>
        <!--  -->
        <template v-else>
          <el-menu-item :index="item.index" :key="item.index" v-show="item.vshow">
            <i :class="item.icon"></i><span slot="title">{{ item.title }}</span>
          </el-menu-item>
        </template>
      </template>
    </el-menu>
  </div>
</template>

<script>
import bus from './bus';
export default {
  name: 'sidebar',
  watch: {
    isSidebarShown: function (val, oldVal) {
      if (val !== oldVal) bus.$emit('sidebar-show', val);
    }
  }, // watch
  data() {
    return {
      collapse: false,
      // user_permission: '',
      isBtnControlSidebar: false,
      isSidebarShown: false,
      items: [
        {
          icon: 'el-icon-location',
          index: 'dashboard',
          title: '',
          forWho: 'all',
          vshow: true
        },
        {
          icon: 'el-icon-date',
          index: '1', // 
          title: '',
          forWho: 'all',
          vshow: true,
          subs: [
            {
              icon: 'el-icon-search',
              index: 'key-point',
              title: '',
              forWho: 'all',
              vshow: true
            },{
              icon: 'el-icon-edit-outline',
              index: 'poem',
              title: '',
              forWho: 'all',
              vshow: true
            }
          ]
        },
        {
          icon: 'el-icon-edit',
          index: '2',
          title: 'Version',
          forWho: 'all',
          vshow: true,
          subs: [
            {
              index: 'test-map',
              title: 'v0.0.0 - ',
              forWho: 'all',
              vshow: true,
            },{
              index: 'map-control',
              title: 'v0.0.2 - ',
              forWho: 'all',
              vshow: true,
            },{
              index: 'map-symbol',
              title: 'v0.0.3 - ',
              forWho: 'all',
              vshow: true,
            },{
              index: 'map-drawer',
              title: 'v0.0.4 - ',
              forWho: 'all',
              vshow: true,
            },{
              index: 'simple-markup',
              title: 'v0.0.5 - ',
              forWho: 'all',
              vshow: true,
            },{
              index: 'popup-markup',
              title: 'v0.0.6 - ',
              forWho: 'all',
              vshow: true,
            },{
              index: 'WMTS-layer',
              title: 'WMTS',
              forWho: 'all',
              vshow: true,
            },{
              index: 'html-2-img',
              title: 'v0.0.8 - ',
              forWho: 'all',
              vshow: true,
            },
          ]
        },
        {
          icon: 'el-icon-picture',
          index: 'one-map',
          title: '',
          forWho: 'all',
          vshow: true
        }
      ]
    }
  },
  computed:{
    onRoutes(){
      return this.$route.path.replace('/',''); //  /  null
    }
  },
  created(){
    // 
    // this.user_permission = sessionStorage.getItem('user_permission')
    // const items = this.items
    // if (this.user_permission !== 'admin') {
    //   for (let i in items) {
    //     if (items[i].forWho === this.user_permission || items[i].forWho === 'all') {
    //       items[i].vshow = true
    //     } else {
    //       items[i].vshow = false
    //     }
    //   }
    // }

    // 
    bus.$on('sidebar-show', val => {
      this.isSidebarShown = val;
    });

    // 
    bus.$on('btn-control-sidebar', e => {
      if (e === false) {
        bus.$off('sidebarResize');
        bus.$once('sidebarResize', win => {
          console.log('');
        });
      } else {
        bus.$off('sidebarResize');
        bus.$on('sidebarResize', win => {
          let width = win.innerWidth;
          if (width <= 800 && this.isSidebarShown) this.isSidebarShown = false;
          else if(width > 800 && !this.isSidebarShown) this.isSidebarShown = true;
        })
      }
    });

    // 
    if (this.isSidebarShown) {
      // 
      if (window.innerWidth <= 500) this.isSidebarShown = false;
      else this.isSidebarShown = true;

      // 
      bus.$off('sidebarResize');
      bus.$on('sidebarResize', win => {
        let width = win.innerWidth;
        if (width <= 800 && this.isSidebarShown) this.isSidebarShown = false;
        else if(width > 800 && !this.isSidebarShown) this.isSidebarShown = true;
      })
    }

    //  Header  Home 
    bus.$emit('sidebar-show', this.isSidebarShown);
  }
}
</script>

<style scoped>
.el-menu{
  border-right-width: 0px;
  width: 175px;
}
.sidebar{
  display: block;
  position: absolute;
  left: 0;
  top: 50px;
  bottom: 0;
  overflow: scroll;
  background-color: rgb(50, 65, 87);
}
.sidebar::-webkit-scrollbar{
  width: 0;
}
.sidebar-el-menu:not(.el-menu--collapse){
  width: 175px;
  height: 100%;
  text-align: left;
}
.sidebar > ul {
  height: 100%;
}
</style>
