import Vue from 'vue'
import Router from 'vue-router'
import Home from './components/Home.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/login'
    },{
      path: '/login',
      name: '',
      component: () => import('./views/Login.vue')
    },
    // 
    {
      path: '/',
      name: 'home',
      component: Home,
      // 
      children: [
        {
          path: '/dashboard',
          name: '',
          component: () => import('./views/Dashboard.vue')
        },{
          path: '/poem',
          name: '',
          component: () => import('./views/poem.vue')
        },{
          path: '/key-point',
          name: '',
          component: () => import('./views/keyPoint.vue')
        },{
          path: '/map-control',
          name: 'v0.0.2 - ',
          component: () => import('./views/mapControl.vue')
        },{
          path: '/map-symbol',
          name: 'v0.0.3 - ',
          component: () => import('./views/mapSymbol.vue')
        },{
          path: '/map-drawer',
          name: 'v0.0.4 - ',
          component: () => import('./views/mapDrawer.vue')
        },{
          path: '/simple-markup',
          name: 'v0.0.5 - ',
          component: () => import('./views/simpleMarkup.vue')
        },{
          path: '/popup-markup',
          name: 'v0.0.6 - ',
          component: () => import('./views/popupMarkup.vue')
        },{
          path: '/test-map',
          name: 'v0.0.0 - ',
          component: () => import('./views/testMap.vue')
        },{
          path: '/WMTS-layer',
          name: 'WMTS',
          component: () => import('./views/map.vue')
        },{
          path: '/html-2-img',
          name: 'v0.0.8 - ',
          component: () => import('./views/htmlToImage.vue')
        },{
          path: '/home-page',
          name: '',
          component: () => import('./views/Home/main.vue')
        },{
          path: '/community',
          name: '',
          component: () => import('./views/Community/main.vue')
        },
      ]
    },
    {
      path: '/one-map',
      name: 'single-map',
      component: () => import('./views/oneMap.vue')
    },
    {
      path: '*',
      redirect: '/dashboard'
    }
  ]
})
