<template>
<!--  div -->
<div :style="EditorDivStyle" v-show="isEditorShown">
    <!-- Tinymce  div -->
    <div id="tinymce" :style="EditorStyle">
        <v-editor  :init='initConfig'></v-editor>
    </div>
    <!--  -->
    <div>
        <el-button type="primary" @click="onSaveContent"></el-button>
    </div>
</div>
</template>

<script>
import vEditor from '@tinymce/tinymce-vue';
import tinymce from '../../public/tinymce/tinymce.min.js';
import '../../public/tinymce/plugins/image/plugin';
import '../../public/tinymce/plugins/imagetools/plugin';
import $ from 'jquery';

import bus from './bus';

export default {
    components: {
        vEditor
    },
    data() {
        return {
            isEditorShown: false,
            EditorDivStyle: {
                display: 'block',
                position: 'absolute',
                overflow: 'hidden',
                right: 0,
                top: '50px',
                bottom: 0,
                height: '',
                width: '',
            },
            EditorStyle: {
                width: '100%',
                margin: '0',
            },
            // TinyMce 
            initConfig: {
                language_url: '/tinymce/langs/zh_CN.js',
                language: 'zh_CN',
                skin_url: '/tinymce/skins/ui/oxide-dark',
                height: window.innerHeight - 150,
                plugins: [
                    "image imagetools save link"
                ],
                toolbar: [
                    'save undo redo | styleselect | bold italic | link image',
                    'alignleft aligncenter alignright | outdent indent'
                ],
                // 
                imagetools_cors_hosts: ['localhost:3033', 'www.tiny.cloud'], // 
                imagetools_proxy: 'proxy.php',
                image_caption: true,
                image_advtab: true,
                branding: false,
                file_picker_callback: function(callback, value, meta) {
                    // Provide image and alt text for the image dialog
                    if (meta.filetype == 'image') {
                        // callback valuemeta
                        callback(value, meta);
                    }
                }, // file_picker_callback
                file_picker_types: 'image',
                images_upload_handler: (blobInfo, success, failure) => {
                    success('http://success.example');
                },
                // 
                save_onsavecallback: function () {
                    bus.$emit('editor-save');
                }
            }
        }
    }, // data
    methods: {
        onSaveContent() {
            const t = window.tinymce;
            const self = this;
            console.log(t.activeEditor.getContent({format: 'raw'}));

            t.activeEditor.focus(); // 
            self.$axios.post('/api/common/save-work', {
                text: '<h1>1</h1><p></p><p><br data-mce-bogus="1"></p><p><img src="https://www.tiny.cloud/docs/images/tiny-husky.jpg" width="320" height="320" alt=""><br data-mce-bogus="1"></p>',
                user: '03030303',
                aaa: 'aaa'
            })
            .then( response => {
                console.log(response);
            })
            .catch( error => {
                if (error) {
                    console.error(error);
                }
            });
        }
    }, // methods
    created() {
        const self = this;
        const t = window.tinymce;
        //  TinyMce 
        t.baseURL += '/tinymce';

        // 
        const HeaderHeight = 50;
        
        let editorHeight = window.innerHeight - HeaderHeight;
        let editorWidth = window.innerWidth * 2 / 5;

        self.EditorDivStyle.height = editorHeight + 'px';
        self.EditorDivStyle.width = editorWidth + 'px';

        // 
        bus.$off('editorResize');
        bus.$on('editorResize', win => {
            let editorHeight = win.innerHeight - HeaderHeight;
            let editorWidth = win.innerWidth * 2 / 5;

            self.EditorDivStyle.height = editorHeight + 'px';
            self.EditorDivStyle.width = editorWidth + 'px';
        });

        // 
        bus.$on('editor-show', isEditorShown => {
            self.isEditorShown = isEditorShown;

            // 
            if (isEditorShown) {
                //  Timeout 
                setTimeout(() => {
                    t.activeEditor.focus();
                }, 0);

                // 
                t.activeEditor.setContent('<h1>1</h1><p></p><p><br data-mce-bogus="1"></p><p><img src="https://www.tiny.cloud/docs/images/tiny-husky.jpg" width="320" height="320" alt=""><br data-mce-bogus="1"></p>');
            }
        });

        // 
        bus.$on('editor-save', _ => {
            self.onSaveContent();
        });
    }
}
</script>

<style scoped>

</style>