<template>
  <div class="header">
    <div class="header-left">
      <!-- Sidebar btn -->
      <div class="btn-sidebar-show" @click="onSidebarShow">
        <el-tooltip effect="dark" :content="btnSidebarShowContent" placement="top-start">
          <div class="icon-menu"></div>
        </el-tooltip>
      </div>
      <!-- Logo -->
      <div class="logo"><img src="../assets/img/logo.png"></div>

      <div class="name">Hi~Story - </div>

      <div class="home" @click="pushHomePage"></div>

      <div class="community" @click="pushCommunityPage"></div>

    </div>

    <div class="header-right">
      <!--  -->
      <div class="btn-editor" @click="isEditorShown = !isEditorShown">
        <el-tooltip effect="dark" :content="'' + (isEditorShown ? '' : '')">
          <i class="el-icon-document"></i>
        </el-tooltip>
      </div>
      <!--  -->
      <div class="btn-fullscreen" @click="handleFullScreen">
        <el-tooltip effect="dark" :content="fullscreen?``:``" placement="bottom">
          <i class="el-icon-rank"></i>
        </el-tooltip>
      </div>
    </div>
  </div>
</template>
<script>
import bus from './bus';
export default {
  name: 'header-component',
  computed: {
    btnSidebarShowContent: function () {
      bus.$emit('sidebar-show', this.isSidebarShown);
      return this.isSidebarShown ? '' : '';
    }
  },
  data() {
    return {
      fullscreen: false,
      isSidebarShown: true,
      isEditorShown: false,
    }
  },// data
  watch: {
    isEditorShown: function (val, oldVal) {
      if (val != oldVal) {
        bus.$emit('editor-show', val);
      }
    }
  },
  methods:{
    // 
    handleFullScreen(){
      let element = document.documentElement;
      if (this.fullscreen) {
        if (document.exitFullscreen) {
          document.exitFullscreen();
        } else if (document.webkitCancelFullScreen) {
          document.webkitCancelFullScreen();
        } else if (document.mozCancelFullScreen) {
          document.mozCancelFullScreen();
        } else if (document.msExitFullscreen) {
          document.msExitFullscreen();
        }
      } else {
        if (element.requestFullscreen) {
          element.requestFullscreen();
        } else if (element.webkitRequestFullScreen) {
          element.webkitRequestFullScreen();
        } else if (element.mozRequestFullScreen) {
          element.mozRequestFullScreen();
        } else if (element.msRequestFullscreen) {
          // IE11
          element.msRequestFullscreen();
        }
      }
      this.fullscreen = !this.fullscreen;
    },
    onSidebarShow () {
      this.isSidebarShown = !this.isSidebarShown;
      //  onresize 
      bus.$emit('btn-control-sidebar', this.isSidebarShown);
    },
    pushHomePage() {
      this.$router.push({
        path: '/home-page',
        query: {
          id: 'user-id'
        }
      })
    },
    pushCommunityPage() {
      this.$router.push({
        path: '/community',
        query: {
          id: 'user-id'
        }
      });
    }
  },//methods
  created() {
    bus.$on('sidebar-show', val => {
      this.isSidebarShown = val;
    });
  }, // created
}
</script>

<style scoped>
.header {
    background-color: rgb(11, 15, 49);
    width: 100%;
    height: 500px;
    color: white;
    font-size: 22px;
}
.header-left {
    height: 50px;
}

/*  */
.btn-sidebar-show, .logo, .name, .home, .community {
    display: table-cell;
    vertical-align: middle;
    height: 50px;
    padding: 0 10px;
}

.name {
    font-size: 28px;
    font-family: '';
}

.home, .community {
  font-size: 20px;
  font-family: '';
}

.btn-sidebar-show, .home, .community{
    cursor: pointer;
}

.logo img {
    height: 50px;
}

/* CSS  */
.icon-menu {
    border-radius: 20px;
    height: 3px;
    width: 30px;
    background-color: white;
    position: relative;
}

.icon-menu::before {
    border-radius: 5px;
    background-color: white;
    content: "";
    height: 3px;
    width: 30px;
    position: absolute;
    top: 10px;
    right: 0;
}

.icon-menu::after {
    border-radius: 5px;
    background-color: white;
    content: "";
    width: 30px;
    height: 3px;
    position: absolute;
    top: -10px;
    right: 0;
}

.header-left {
    float: left;
}

.header-right {
    float: right;
}

.btn-editor,.btn-fullscreen {
    cursor: pointer;
    display: table-cell;
    vertical-align: middle;
    height: 50px;
    padding: 0 10px;
}

.btn-fullscreen {
    transform: rotate(45deg);
}
</style>
